import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ConfigProvider } from 'src/providers/utils/config';
import { UtilService } from 'src/providers/utils/utils.service';
import { MediaReleaseService } from './mediaService';

@Component({
  selector: 'app-media-release',
  templateUrl: './media-release.page.html',
  styleUrls: ['./media-release.page.scss'],
})
export class MediaReleasePage implements OnInit {

  medialist = [];
  loadMoreTimer;
  isServiceCallInProgress: any = false;
  isLastPageReached: boolean = false;
  hasRecords: any = true;
  pageNum: number = 1;
  errorMsg: any;

  constructor(public mediaReleaseService: MediaReleaseService, public utils: UtilService, public translate: TranslateService) { }

  ngOnInit() {
    this.getMediaList(undefined);
  }

  getMediaList(event) {
    // this.isLastPageReached = false;
    this.utils.showProgressLoader();
    this.mediaReleaseService.getMediaRelease(this.pageNum).then(
      (result) => {
        try {
          this.utils.dissmisProgressLoader();
          if (this.medialist.length > 0) {
            this.medialist = this.medialist.concat(result['responseJSON']['array']);
          } else {
            this.medialist = result['responseJSON']['array'];
            ConfigProvider.PAGE_SIZE = result['responseJSON']['pageSize'];
          }
          if (!(this.medialist.length > 0)) {
            if (result['responseJSON']['array'].length < ConfigProvider.PAGE_SIZE) {
              this.isLastPageReached = true;
            } else {
              this.pageNum++;
            }
          } else {
            if (result['responseJSON']['array'].length < ConfigProvider.PAGE_SIZE) {
              this.isLastPageReached = true;
            } else {
              this.pageNum++;
            }
          }
        } catch (e) {
          this.utils.dissmisProgressLoader();
          this.errorMsg = this.translate.instant('generic_msgs.server_err');
        }
      },
      (err) => {
        this.utils.dissmisProgressLoader();
        this.errorMsg = this.utils.handleServiceException(err);
      }
    );
  }

  openLink(link) {
    if (link != null && link != undefined) {
      // link = link.replace('bpp1.', '');
      this.utils.openLink(link);
    } else {
      this.utils.presentToast(this.translate.instant('generic_msgs.network_err'));
    }
  }

  getMoreMedia(event) {
    this.getMediaList(event);
  }

}
